  z-index: 1;
  font-size: 1rem;
  line-height: 1.78;
  font-weight: normal;
  padding: 0.75em 2.125em;
  color: #f1f1f1;
  transition: all 0.2s ease-out;
}
@media only screen and (min-width: 48em) {
  .btn {
    font-size: 1.125rem;
  }
}
.btn svg path {
  fill: #10ADD9;
}
.btn:hover {
  cursor: pointer;
}
.btn:hover svg path {
  fill: #ffffff;
}

.btn--primary {
  background: #10ADD9;
  border: 1px solid #ffffff;
}

.btn--secondary {
  background: #000000;
  border: 1px solid #ffffff;
}

.btn--outline {
  background: transparent;
  color: #10ADD9;
  border: 1px solid #10ADD9;
}

.btn--transparent {
  background: transparent;
  color: #10ADD9;
  display: inline-flex;
  align-items: center;
  gap: 1rem;
  padding-inline: 0;
}
.btn--transparent:hover {
  padding-inline: 1em;
}
.btn--transparent:hover svg path {
  fill: #ffffff;
}

/*
---------------------------------------
hover effect
---------------------------------------
*/
.btn--primary::before,
.btn--secondary::before,
.btn--outline::before {
  content: "";
  top: 0;
  left: 50%;
  right: 50%;
  bottom: 0;
  position: absolute;
  opacity: 0;
  background-color: #1a232a;
  z-index: -1;
  transition: all 0.25s ease-out;
}

.btn--secondary:hover, .btn--secondary:focus,
.btn--primary:hover,
.btn--primary:focus,
.btn--outline:hover,
.btn--outline:focus {
  color: #ffffff;
}
.btn--secondary:hover::before, .btn--secondary:focus::before,
.btn--primary:hover::before,
.btn--primary:focus::before,
.btn--outline:hover::before,
.btn--outline:focus::before {
  transition: all 0.25s ease-out;
  left: 0;
  right: 0;
  opacity: 1;
}
.btn--secondary.active,
.btn--primary.active,
.btn--outline.active {
  transform: scale(0.9);
}

.btn--secondary::before,
.btn--outline::before {
  background: #10ADD9;
}

